.main {
  height: 100vh;
  overflow: hidden;
  position: relative;

  .product-wrap {
    width: 100%;
    padding-top: 50px;
    background: #000;

    .video {
      width: 100%;
      height: auto;
      filter: blur(3px);
      transition: filter 0.3s ease;
      opacity: 0.8;
      &:hover {
        filter: blur(0);
      }
    }
  }
  .mainWrap {
    .txt_wrap {
      position: absolute;
      top: 50%;
      left: 50%;
      z-index: 2;
      width: 642px;
      text-align: center;
      margin-left: -321px;
      margin-top: -20npm px;

      > h3 {
        font-size: 5rem;
        color: #fff;
        line-height: 3.8rem;
        font-family: "Roboto";
        display: inline-block;
        height: 100%;
      }
      > a {
        > button {
          width: 200px;
          height: 47px;
          color: #fff;
          font-size: 1rem;
          font-family: "Roboto";
          font-weight: 500;
          margin-top: 45px;
          border: none;
          cursor: pointer;
          background: #fff;
          transition: 0.5s;
          color: #000;

          &:hover {
            background: #000;
            color: #fff;
          }
        }
      }
    }
  }
}

@media all and (max-width: 1440px) {
  .main {
    height: auto;
    padding-top: 53px;
    .product-wrap {
      overflow: hidden;
      .video {
        margin-top: 0;
      }
    }
    .mainWrap {
      .txt_wrap {
        width: 671px;
        margin-left: -335px;
        margin-top: -46px;
        h3 {
          font-size: 6vw;
        }
        a {
          button {
            font-size: 1.2vw;
          }
        }
      }
    }
  }
}
