.banner-wrap {
  width: 100%;
  height: 335px;

  box-sizing: border-box;
  > p {
    text-align: center;
    font-size: 1.4rem;
    font-weight: 400;
    font-family: "Noto Sans KR", sans-serif;
    position: relative;
    &::after {
      content: "";
      display: block;
      width: 1px;
      height: 70px;
      background: #000;
      position: absolute;
      left: 50%;
      top: 97px;
    }
  }
}

@media all and (max-width: 768px) and (min-width: 600px) {
  .banner-wrap {
    height: 320px;
    p {
      font-size: 2.8vw;
    }
  }
}
@media all and (max-width: 600px) and (min-width: 425px) {
  .banner-wrap {
    height: 230px;
    p {
      font-size: 3.5vw;
    }
  }
}
@media all and (max-width: 425px) and (min-width: 320px) {
  .banner-wrap {
    height: 225px;
    p {
      font-size: 3.5vw;
    }
  }
}
