.sport {
  width: 1250px;
  padding-top: 158px;
  position: relative;
  > h3 {
    font-size: 4rem;
    font-weight: 800;
    display: block;
    width: 100%;
    text-align: center;
  }

  .sport-filters {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin: 50px 0;
    flex-wrap: wrap;
    button {
      padding: 5px 20px;
      margin: 0;
      font-family: "Noto Sans KR", sans-serif;
      font-size: 18px;
      background: none;
      border: 1.5px solid #000;
      border-radius: 60px;
      cursor: pointer;
    }
  }
  .sportWrap {
    box-sizing: border-box;
    height: 518px;
    overflow: hidden;
    padding-top: 120px;
    .slide {
      height: 290px;
      cursor: pointer;

      .sport-width {
        width: 100%;
      }
      .sport-height {
        height: 500px;
      }
    }
  }
}
.swiper-button-prev {
  position: absolute !important;
  top: 40px !important;
  left: 1133px !important;
  width: 50px !important;
  height: 50px !important;
  border: 2px solid #a6a6a6;
  border-radius: 50%;
  transition: 0.5s;

  &:hover {
    background: #000;
    border: 2px solid #000;
  }
  &:hover:after {
    color: #fff;
  }
}
.swiper-button-prev:after {
  color: #a6a6a6;
  text-align: center;
  line-height: 50px !important;
  font-size: 1.4rem !important;
  font-weight: 600 !important;
}
.swiper-button-next {
  position: absolute !important;
  top: 40px !important;
  right: 0 !important;
  width: 50px !important;
  height: 50px !important;
  border: 2px solid #a6a6a6;
  border-radius: 50%;
  transition: 0.5s;
  &:hover {
    background: #000;
    border: 2px solid #000;
  }
  &:hover:after {
    color: #fff;
  }
}
.swiper-button-next:after {
  color: #a6a6a6;
  text-align: center;
  line-height: 50px;
  border-radius: 50%;
  font-size: 1.4rem !important;
  font-weight: 600 !important;
}

