.product {
  width: calc(100% / 4 - 20px);
  background: #f4f4f4;
  height: 390px;
  overflow: hidden;
  cursor: pointer;
  position: relative;

  .bookmark {
    position: absolute;
    font-size: 1.5rem;
    right: 25px;
    top: 25px;
    cursor: pointer;
    color: #a7a7a7;
    z-index: 2;
    &.on {
      color: #ff3a3a;
    }
  }

  .bookmark-on {
    position: absolute;
    font-size: 1.5rem;
    right: 20px;
    top: 20px;
    cursor: pointer;
    opacity: 0;
  }

  &:hover img {
    transform: scale(1.1);
  }
  .image-wrapper {
    .image {
      width: 100%;
      transition: 0.3s;
      margin-top: -45px;
    }
  }
  .wrap {
    position: absolute;
    top: 284px;
    left: 0;
    display: flex;
    align-items: flex-start;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
    justify-content: space-between;
    z-index: 3;

    .txt-wrap {
      margin: 0;
      h3 {
        font-family: "Noto Sans KR", sans-serif;
        font-size: 1.1rem;
        padding: 10px 0;
      }
      .p {
        font-family: "Noto Sans KR", sans-serif;
        font-weight: 500;
        color: #9f9f9f;
      }
      .subTitle {
        color: #d31919;
        font-family: "Noto Sans KR", sans-serif;
        font-weight: 500;
      }
      .hex {
        width: 20px;
        height: 24px;
        background: none;
        border: none;
        cursor: pointer;
        transition: 0.3s;

        &::-webkit-color-swatch {
          border: 1.3px solid #282828;
        }

        &:hover {
          transform: scale(1.1);
        }
      }
    }
    .price-wrap {
      margin: 0;
      .price {
        padding-top: 35px;
        font-family: "Noto Sans KR", sans-serif;
        font-size: 1.1rem;
        font-weight: 600;
        letter-spacing: 0.2px;
      }
    }
  }
}

@media all and (max-width: 1024px) and (min-width: 800px) {
  .product {
    width: calc(100% / 4 - 14px);
    height: 350px;
    .wrap {
      top: 243px;
      .txt-wrap {
        h3 {
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }
    .image-wrapper {
      .image {
        margin-top: -16px;
      }
    }
  }
}
@media all and (max-width: 800px) and (min-width: 768px) {
  .product {
    width: calc(100% / 4 - 14px);
    height: 327px;
    .wrap {
      top: 182px;
    }
    .image-wrapper {
      .image {
        margin-top: 0;
      }
    }
  }
}
